    background-color: rgb(240, 143, 240);
    padding: 20px;
    
}
h1{
    text-align: center;
}
h2{
    text-align: center;
    color: blueviolet;
}
body {
    margin: 0%;
}
.topnav {
    overflow: hidden;
    background-color: #333;
  }
.topnav a {
    float: right;
    display: block;
    color: #f15757;
    text-align: center;
    padding: 14px 16px;
    text-decoration: black;
  }
.topnav a:hover{
    background-color: #ddd;
    color: black;
}
*{
    margin: 0%;
    padding: 0%;
}
.sidebar{
    position: absolute;
    width: 17%;
    height: 100%;
    
    background-color: black;
}
.sidebar li{
    color: white;
    font-size: 30px;
    padding: 30px;
    border-bottom: 1px solid grey;
    text-align: center;
}
.footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 36px;
    background-color: rgb(25, 131, 218);
    color: white;
    text-align: center;
  }
  .Mcontent{
      margin: auto;
  }
  .youtube{
    padding-left: 22%;
    padding-top: 30px;
}
.styl{
